@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+SC:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
  color: lightgray;
}
body {
  margin: 0;
  font-family: 'Noto Sans SC', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1200px;
  margin: 0 auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{padding:10px;text-align:left}.win-card{background-color:#4da375}.lost-card{background-color:#a34d5d}

.MatchDetailCard{display:grid;grid-template-columns:repeat(2, 1fr);padding:5px;margin-top:10px}.MatchDetailCard .match-date .match-venue .match-result{margin-top:5px}.MatchDetailCard .main-info{grid-column:span 1}.MatchDetailCard .main-info .team-name{font-size:2rem}.MatchDetailCard .additional-details{grid-column:span 1;text-align:right}.MatchDetailCard .additional-details>p{margin-bottom:10px}

.MatchSmallCard{padding:5px}.MatchSmallCard .team-name{font-size:1.5rem}.MatchSmallCard .match-result{margin-top:5px}

.TeamPage{display:grid;grid-template-columns:repeat(4, 1fr);grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-secton{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name-secton .team-name{font-size:3rem}.TeamPage .win-loss-section{grid-column:span 1;text-align:center;padding:20px;height:200px}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-section{-webkit-align-self:center;align-self:center;justify-self:center;font-size:1.5rem}

.MatchPage{display:grid;grid-template-columns:1fr 6fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:end;margin-top:10px}

.YearSelector{justify-self:end;padding:5px;text-align:right}.YearSelector li{list-style-type:none;margin-bottom:10px}

.AllTeamsPage{display:grid;grid-template-columns:repeat(2, 1fr);grid-row-gap:20px;grid-column-gap:20px;margin-top:10px}.AllTeamsPage span{padding-bottom:50px;padding-top:10px;text-align:center;box-shadow:5px;border:1px solid lightgray}

